exports.push([module.id, ".bots-list[data-v-3173ac51] {\n  list-style: none;\n}\n.nowrap[data-v-3173ac51] {\n  white-space: nowrap;\n}\n.white-text[data-v-3173ac51] {\n  color: white;\n}\n", "",{"version":3,"sources":["/Users/s.dastan/Desktop/chatwoot/chatwoot/app/javascript/dashboard/routes/dashboard/settings/agentBots/app/javascript/dashboard/routes/dashboard/settings/agentBots/Index.vue"],"names":[],"mappings":"AA6FA;EACA,gBAAA;AACA;AACA;EACA,mBAAA;AACA;AACA;EACA,YAAA;AACA","file":"Index.vue","sourcesContent":["<template>\n  <div class=\"column content-box\">\n    <div class=\"row\">\n      <div class=\"small-8 columns with-right-space\">\n        <woot-loading-state\n          v-if=\"uiFlags.isFetching\"\n          :message=\"$t('AGENT_BOTS.LIST.LOADING')\"\n        />\n        <table v-else-if=\"agentBots.length\" class=\"woot-table\">\n          <tbody>\n            <agent-bot-row\n              v-for=\"(agentBot, index) in agentBots\"\n              :key=\"agentBot.id\"\n              :agent-bot=\"agentBot\"\n              :index=\"index\"\n              @delete=\"onDeleteAgentBot\"\n              @edit=\"onEditAgentBot\"\n            />\n          </tbody>\n        </table>\n        <p v-else class=\"no-items-error-message\">\n          {{ $t('AGENT_BOTS.LIST.404') }}\n        </p>\n      </div>\n\n      <div class=\"small-4 columns content-box\">\n        <p v-html=\"$t('AGENT_BOTS.SIDEBAR_TXT')\" />\n      </div>\n    </div>\n    <woot-button\n      color-scheme=\"success\"\n      class-names=\"button--fixed-right-top\"\n      icon=\"add-circle\"\n    >\n      <router-link :to=\"newAgentBotsURL\" class=\"white-text\">\n        {{ $t('AGENT_BOTS.ADD.TITLE') }}\n      </router-link>\n    </woot-button>\n    <woot-confirm-modal\n      ref=\"confirmDialog\"\n      :title=\"$t('AGENT_BOTS.DELETE.TITLE')\"\n      :description=\"$t('AGENT_BOTS.DELETE.DESCRIPTION')\"\n    />\n  </div>\n</template>\n\n<script>\nimport { mapGetters } from 'vuex';\nimport { frontendURL } from '../../../../helper/URLHelper';\nimport AgentBotRow from './components/AgentBotRow.vue';\nimport alertMixin from 'shared/mixins/alertMixin';\n\nexport default {\n  components: { AgentBotRow },\n  mixins: [alertMixin],\n  computed: {\n    ...mapGetters({\n      accountId: 'getCurrentAccountId',\n      agentBots: 'agentBots/getBots',\n      uiFlags: 'agentBots/getUIFlags',\n    }),\n    newAgentBotsURL() {\n      return frontendURL(\n        `accounts/${this.accountId}/settings/agent-bots/csml/new`\n      );\n    },\n  },\n  mounted() {\n    this.$store.dispatch('agentBots/get');\n  },\n  methods: {\n    async onDeleteAgentBot(bot) {\n      const ok = await this.$refs.confirmDialog.showConfirmation();\n      if (ok) {\n        try {\n          await this.$store.dispatch('agentBots/delete', bot.id);\n          this.showAlert(this.$t('AGENT_BOTS.DELETE.API.SUCCESS_MESSAGE'));\n        } catch (error) {\n          this.showAlert(this.$t('AGENT_BOTS.DELETE.API.ERROR_MESSAGE'));\n        }\n      }\n    },\n    onEditAgentBot(bot) {\n      this.$router.push(\n        frontendURL(\n          `accounts/${this.accountId}/settings/agent-bots/csml/${bot.id}`\n        )\n      );\n    },\n  },\n};\n</script>\n<style scoped>\n.bots-list {\n  list-style: none;\n}\n.nowrap {\n  white-space: nowrap;\n}\n.white-text {\n  color: white;\n}\n</style>\n"]}]);
